.adaptiveMenu {
  background-color: #C0C0C0;
  width: 1200px;
  height: 1200px;
  border: 3px solid black;
}
.adaptiveMenu .close {
  width: 150px;
  margin: 22px 0 0 0;
  float: right;
}
.adaptiveMenu .navbar .nav a {
  text-decoration: none;
  font-size: 43px;
  color: black;
  display: flex;
  justify-content: center;
  align-items: center;
}
.adaptiveMenu .navbar .nav .hr {
  margin: 80px 0;
  background-color: black;
}